.mainForm {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 200px;
  width: 300px;
  padding: 20px;
  border-color: black;
  border-style: hidden;
  background-color: white;
  -webkit-box-shadow: 0px 10px 13px -7px #000000,
    5px 6px 14px 4px rgba(0, 0, 0, 0);
  box-shadow: 0px 10px 13px -7px #000000, 5px 6px 14px 4px rgba(0, 0, 0, 0);
  font-family: 'Times New Roman', Times, serif;
  opacity: 0.8;
}

.formInput {
  outline: 20;
  outline-color: #000000;
  width: 100%;
  margin-bottom: 10px;
  border-radius: 20px;
  padding: 5px;
  background-color: white;
  box-shadow: inset;
}

.formLabel {
  font-size: 20px;
  font-weight: 500;
  line-height: normal;
  color: black;
  display: block;
  margin-bottom: 10px;
}

.addButton {
  border-radius: 20px;
  margin: 0 auto;
  padding: 10px 20px;
  font-size: 20px;
  font-weight: 100px;
  line-height: normal;
}

.addButton:hover {
  background-color: #628dde;
  cursor: pointer;
}
